// 
// _buttons.scss
// 

button,a {
  outline: none !important;
}


// Rounded Buttons
.btn-rounded {
  border-radius: 30px;
}

// theme dark
[data-layout-mode="dark"]{

   
  .btn-light{
      color: var(--#{$prefix}dark);
      background-color: var(--#{$prefix}light);
      border-color: var(--#{$prefix}light);
      &:hover{
          color: var(--#{$prefix}dark);
          background-color: rgba(var(--#{$prefix}light-rgb), 0.75);
          border-color: rgba(var(--#{$prefix}light-rgb), 0.75);
      }
  }

  .btn-check:focus+.btn-light,
  .btn-check:focus+.btn-outline-light,
  .btn-check:focus+.btn-soft-light,
  .btn-light:focus,
  .btn-outline-light:focus,
  .btn-soft-light:focus{
      color: var(--#{$prefix}dark);
      background-color: rgba(var(--#{$prefix}light-rgb), 0.75);
      border-color: rgba(var(--#{$prefix}light-rgb), 0.75);
  }

  .btn-check:active+.btn-light,
  .btn-check:active+.btn-outline-light,
  .btn-check:active+.btn-soft-light,
  .btn-check:checked+.btn-light,
  .btn-check:checked+.btn-outline-light,
  .btn-check:checked+.btn-soft-light,
  .btn-light.active, .btn-light:active,
  .btn-outline-light.active, .btn-outline-light:active,
  .btn-soft-light.active, .btn-soft-light:active,
  .show>.btn-light.dropdown-toggle,
  .show>.btn-outline-light.dropdown-toggle,
  .show>.btn-soft-light.dropdown-toggle{
      color: var(--#{$prefix}dark);
      background-color: rgba(var(--#{$prefix}light-rgb), 0.75);
      border-color: rgba(var(--#{$prefix}light-rgb), 0.75);
  }

  // dark

  .btn-dark{
      color: var(--#{$prefix}light);
      background-color: var(--#{$prefix}gray-100);
      border-color: transparent;
      
      &:hover{
          color: var(--#{$prefix}light);
          background-color: var(--#{$prefix}gray-100);
          border-color: transparent;
      }
  }

  .btn-check:focus+.btn-dark, 
  .btn-check:focus+.btn-outline-dark,
  .btn-check:focus+.btn-soft-dark,
  .btn-dark:focus,
  .btn-outline-dark:focus,
  .btn-soft-dark:focus{
      color: var(--#{$prefix}light);
      background-color: var(--#{$prefix}gray-100);
      border-color: transparent;
  }

  .btn-check:active+.btn-dark,
  .btn-check:active+.btn-outline-dark,
  .btn-check:active+.btn-soft-dark,
  .btn-check:checked+.btn-dark,
  .btn-check:checked+.btn-outline-dark,
  .btn-check:checked+.btn-soft-dark,
  .btn-dark.active, .btn-dark:active,
  .btn-outline-dark.active, .btn-outline-dark:active,
  .btn-soft-dark.active, .btn-soft-dark:active,
  .show>.btn-dark.dropdown-toggle,
  .show>.btn-outline-dark.dropdown-toggle,
  .show>.btn-soft-dark.dropdown-toggle{
      color: var(--#{$prefix}light);
      background-color: var(--#{$prefix}gray-100);
      border-color: transparent;
  }

  // outline

  .btn-outline-light{
      color: var(--#{$prefix}dark);
      border-color: var(--#{$prefix}light);
      &:hover{
          color: var(--#{$prefix}dark);
          background-color: rgba(var(--#{$prefix}light-rgb), 0.75);
          border-color: rgba(var(--#{$prefix}light-rgb), 0.75);
      }
  }

  .btn-outline-dark{
      color: var(--#{$prefix}gray-100);
      border-color: var(--#{$prefix}gray-100);
      &:hover{
          color: var(--#{$prefix}dark);
          background-color: var(--#{$prefix}gray-100);
          border-color: var(--#{$prefix}gray-100);
      }
  }

  // soft

  .btn-soft-light{
      color: var(--#{$prefix}dark);
      background-color: rgba(var(--#{$prefix}light-rgb), 0.1);
      &:hover{
          color: var(--#{$prefix}dark);
          background-color: var(--#{$prefix}light);
      }
  }

  .btn-soft-secondary{
      color: var(--#{$prefix}gray-600);
  }

  .btn-soft-dark{
      color: var(--#{$prefix}dark);
      background-color: rgba(var(--#{$prefix}dark-rgb), 0.1);
      &:hover{
          color: var(--#{$prefix}light);
          background-color: var(--#{$prefix}dark);
      }
  }
}

